@charset "utf-8";

/****************************************************************************
  상황판 한정 스타일
***************************************************************************/
#container.overview {
  /* 공통 */
  .l-article {
    padding: 1rem 2rem 2rem;
    &-header {
      height: 4.5rem;
      &__title {
        font-size: 2rem;
        font-weight: 300;
        line-height: 2.9rem;
      }
    }
    &--bg-gradient {
      border: 1px solid $G-default-border-color;
      border-radius: $border-radius-xl;
      background: $G-default-gradient;
    }
  }

  .cnt {
    font-family: "Montserrat";
  }
  .fs-16 {
    font-size: 1.6rem;
  }
  .fs-18 {
    font-size: 1.8rem;
  }
  .fs-20 {
    font-size: 2rem;
  }
  .fs-24 {
    font-size: 2.4rem;
  }
  .fw-200 {
    font-weight: 200;
  }
  .fw-400 {
    font-weight: 400;
  }
  .fw-700 {
    font-weight: 700;
  }
  .color-666 {
    color: #666666;
  }
  .color-999 {
    color: #999;
  }
  .flex-nowrap {
    flex-wrap: nowrap;
  }
  .align-center {
    align-items: center;
  }
  .justify-content-around {
    justify-content: space-around;
  }

  // animation ready
  .l-article--bg-gradient.is-ready {
    opacity: 0;
    transform: translateY(4rem);
    transition: none;
  }

  // animation start
  .l-article--bg-gradient.is-start {
    opacity: 1;
    transform: translateY(0);
    @include new-transition($tgs: "opacity, transform", $time: 0.7s);
  }

  // animation complete
  .l-article--bg-gradient.is-complete {
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */
  @import "./overview__swiper-slide-overview1";
  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */
  @import "./overview__swiper-slide-overview2";
  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */
}
