/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
    header
****************************************************************************/
/* header */
#header {
  height: 8.6rem;
  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  .header-logo {
    display: flex;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 3rem;
    transform: translateY(-50%);
    &__link {
      display: flex;
      align-items: center;
    }
    .img {
      width: auto;
      height: 3.4rem;
    }
    .tit {
      color: #666666;
      padding-left: 1rem;
      font-size: 3.2rem;
    }
    .desc {
      position: relative;
      padding-left: 1rem;
      margin-left: 1rem;
      font-size: 3.2rem;

      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        width: 1px;
        height: 3rem;
        background: #8a8a8a;
        margin-top: -1.5rem;

      }
    }
  }
  
  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  .header-indicator {
    display: flex;
    align-items: center;
    position: absolute;
    top: 50%;
    right: 3rem;
    transform: translateY(-50%);


    &__item {
      color: #999999;
      font-size: 2rem;

      > .btn {
        margin-left: 1rem;
        background: #222222;
        border: 1px solid #606060;
        font-size: inherit;
        .icon-svg {
          &:after {
            background-color: #cccccc;
          }
        }
        &:hover {
          .icon-svg {
            &:after {
              background-color: #ffffff;
            }
          }
        }
      }
    }


  }  

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */
}
