.swiper-slide-overview2 {
  .cnt {
    font-family: "Montserrat";
  }

  .fs-16 {
    font-size: 1.6rem;
  }
  .fs-18 {
    font-size: 1.8rem;
  }
  .fs-20 {
    font-size: 2rem;
  }
  .fs-24 {
    font-size: 2.4rem;
  }
  .fw-200 {
    font-weight: 200;
  }
  .fw-400 {
    font-weight: 400;
  }
  .fw-700 {
    font-weight: 700;
  }
  .color-666 {
    color: #666666;
  }
  .color-999 {
    color: #999;
  }
  .flex-nowrap {
    flex-wrap: nowrap;
  }
  .align-center {
    align-items: center;
  }
  .justify-content-around {
    justify-content: space-around;
  }

  .c-badge {
    display: flex;
    align-items: center;
    height: 3rem;
    padding: 0 1.2rem;
    border-radius: 100px;
    font-size: 1.8rem;
  }

  [data-article="관련뉴스"] {
    .border-bottom {
      border-color: #606060 !important;
    }
    .title-box {
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 100%;
      text-align: center;
    }
    .title {
      display: block;
      width: 100%;
      font-size: 2.8rem;
      text-align: center;
    }

    .news-list {
      display: flex;
      flex-direction: column;
      justify-content: space-around;

      > li {
        position: relative;
        padding-left: 1.8rem;
        font-weight: 300;

        &:before {
          content: "";
          position: absolute;
          top: 50%;
          left: 0;
          width: 0.8rem;
          height: 0.8rem;
          background: rgba(255, 255, 255, 0.3);
          margin-top: -0.4rem;
          border-radius: 50%;
        }
      }
    }
  }

  [data-article="TPOP"] {
    .l-article-header {
      .cnt {
        color: #cccccc;
      }
    }
    ul {
      li {
        .cnt {
          color: #737373;
        }
      }
    }
  }
}
