/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
  section - #container 구성하는 부모와 자식 관계
****************************************************************************/
/* 기본 속성 */
.l-section {
  position: relative;
  &-header {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    position: relative;
    min-width: 100%;
    &__title {
      display: flex;
      align-items: center;
      position: relative;
      font-weight: 600;
      line-height: 1em;
    }
  }
  &-body {
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: 0%;
    position: relative;
    min-width: 100%;
    height: 100%;
  }
  &-footer {
    position: relative;
  }
  > [class^="inner"] {
    display: flex;
    flex-direction: column;
  }
}

/* Project Custom Style */
.l-section {
  @extend .l-section;
}

/****************************************************************************
  article - 기본 콘텐츠 레이아웃
****************************************************************************/
/* 기본 속성 */
.l-article {
  display: flex;
  flex-direction: column;
  height: 100%;
  &-header {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    position: relative;
    width: 100%;
    &__title {
      display: flex;
      align-items: center;
      position: relative;
      line-height: 1em;
      font-weight: 600;
    }
  }
  &-body {
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: 0%;
    position: relative;
    height: 100%;
    &--flex-wrap {
      display: flex;
      flex-flow: wrap;
    }
  }
  &-footer {
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: 0%;
    position: relative;
    height: 100%;
  }
}
