/* inline icons */
@mixin icon-svg($icon, $color: $G-color-primary) {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 1em;
  height: 1em;
  font-size: var(--font-size, $G-font-size);
  line-height: 1em;
  &::after {
    @include icon-svg-after($icon, $color)
  }
}

.icon-svg {
  $names: close, back, circle, check, calendar, classify, memo, chart-word-cloud, normal-board, triangle-down, triangle-up, search, interj, bar, play, arrow-up,
    bar, user, lock, eye, eye-slash, thumbs-up, fire, chevron-up, chevron-down, chevron-left, chevrons-left, chevron-right, chevrons-right, twitter, facebook,
    emotion-positive, emotion-neutral, emotion-negative, noty-info, noty-success, noty-error, pin, logout, system-monitor, smile, update, warning, download, refresh, home, chart-candle, chart-line, chart-map, channel-news , channel-community ,channel-blog ,channel-cafe ,channel-twitter ,channel-instagram ,channel-youtube ,channel-facebook ,channel-kakaostory ,channel-kin ,channel-organization ,channel-government, job-student,job-graduate-student,job-none,job-army,job-housewife,job-businessman,job-office-worker,job-developer,job-researcher,job-specialized,job-civil-servant,job-radioman,job-statesman,job-religious,job-creator,job-etc, interest-it, interest-medical, interest-game, interest-culture, interest-animal, interest-infant-care, interest-fashion, interest-beuty, interest-social-issue, interest-sports, interest-food, interest-trip, interest-entertainment, interest-interior, interest-car, interest-conservatism, interest-progressive, interest-politics, interest-feminism, interest-enviroment, interest-economy, interest-edu;

  @each $name in $names {
    &#{"-" + $name} {
      @include icon-svg($name);
    }
  }
}


