/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

.c-chart-bar {
  $comp: c-chart-bar;
  $small: 1.8rem;
  $normal: 3rem;
  width: 100%;
  position: relative;
  padding: 1.5rem 0 1.5rem 2rem;
  box-sizing: unset;

  // default - (horizontal/normal)
  .c-bullet {
    position: absolute;
    top: 50%;
    left: 0;
    margin-top: -3rem;
    filter: drop-shadow(4px 0px 4px rgba(0, 0, 0, 0.25));
  }
  &__back {
    display: block;
    width: 100%;
    height: $normal;
    background: #222222;
    border-radius: 100px;
  }
  &__bar {
    display: block;
    height: 100%;
    border-radius: 100px;
  }

  &--small {
    .#{$comp}__back {
      height: $small;
    }
    .c-bullet {
      width: 4rem;
      height: 4rem;
      margin-top: -2rem;
    }
  }

  // vertical
  &--vertical {
    height: 100%;
    width: $normal;
    padding: 0;

    .#{$comp}__back {
      position: relative;
      width: $normal;
      height: 100%;
    }
    .#{$comp}__bar {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    &.#{$comp}--small {
      width: $small;
      .#{$comp}__back {
        width: $small;
        height: 100%;
      }
      .c-bullet {
        width: 4rem;
        height: 4rem;
        margin-top: -2rem;
      }
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

.c-bullet {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 6rem;
  height: 6rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 100px;
  background: #222222;
  box-sizing: border-box;

  [class*="icon-svg"] {
    width: 2.8rem;
    height: 2.8rem;
    &:after {
      width: 2.8rem;
      height: 2.8rem;
      background-color: var(--icon-color, #ffffff);
    }
  }

  &--small {
    [class*="icon-svg"] {
      width: 2rem;
      height: 2rem;
      &:after {
        width: 2rem;
        height: 2rem;
      }
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

//data-article="전체정보량"
[data-article="전체정보량"] {
  .cnt {
    font-size: 7rem;
    font-weight: 700;
    line-height: 8.5rem;
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

//data-article="호감도SNPS"
[data-article="호감도SNPS"] {
  .c-chart-gauge {
    position: relative;
    display: flex;
    justify-content: center;
    margin-top: 1.7rem;
    &__bar {
      display: inline-block;
      width: 13rem;
      height: 13rem;
      background-size: cover;
      &--nega {
        background-image: url(../../img/common/chart-nega.png);
      }
      &--posi {
        background-image: url(../../img/common/chart-posi.png);
      }
    }
    &__arrow {
      position: absolute;
      bottom: 0rem;
      left: 50%;
      width: 4.8rem;
      height: calc(13rem - 2.8rem);
      margin-left: -2.3rem;
      background: url(../../img/common/chart-arrow.png) no-repeat 0 0;
      transform-origin: bottom;
      @include new-transition($tgs: "transform", $time: 0.7s);
    }
    &__num {
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      bottom: -6.5rem;
      left: 50%;
      height: 13rem;
      width: 13rem;
      margin-left: -6.5rem;
      background: #4a4a4a;
      border: 0.4rem solid #575757;
      border-radius: 100%;
      font-size: 2.8rem;
      font-weight: 700;
      line-height: 3.4rem;
      font-family: "Montserrat";
      &::after {
        content: "%";
        display: inline-block;
      }
      &.is-negative {
        &::before {
          content: "-";
          display: inline-block;
        }
      }
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

[data-article="Top5채널"] {
  .l-article-body {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .cnt {
    &::after {
      display: inline-block;
      content: "%";
    }
  }
  .c-chart-bar {
    box-sizing: border-box;
    & + .cnt {
      width: 8rem;
      text-align: right;
    }
  }
  .c-chart-bar__bar {
    width: 0;
    @include new-transition($tgs: "width", $time: 0.7s);
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

[data-article="성별"] {
  .js-chart {
    height: calc(100% - 4.5rem);
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

[data-article="연령별"] {
  .c-chart-bar {
    height: calc(100% - 7rem);
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

[data-article="Top3직업"],
[data-article="Top3관심사"] {
  ul {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 100%;

    .cnt {
      text-align: right;
    }
    .c-bullet {
      flex: 0 6rem;
      & + span {
        flex: 1;
        line-height: 1;
        text-align: right;
        word-break: keep-all;
      }
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/
