@charset "utf-8";

/****************************************************************************
  회원정보 한정 스타일
***************************************************************************/
#container.regional {
  display: flex;
  align-items: center;
  min-height: 95vh;

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  [data-section="지역설정"] {
    .l-section-header {
      flex-direction: column;
      margin-bottom: 7rem;

      &__title {
        color: #ed1c24;
        font-size: 9rem;
        line-height: 13rem;
      }
      &__description {
        margin-top: 1.5rem;
        font-size: 3.2rem;
        line-height: 4.6rem;
      }
    }
    .list-regional {
      display: flex;
      &__item {
        padding: 0 15px;
        .btn-regional {
          display: flex;
          flex-direction: column;
          justify-content: flex-start;
          width: 16rem;
          height: 25rem;
          padding: 2rem;
          border: 1px solid $G-default-border-color;
          border-radius: $border-radius-xl;
          background: $G-default-gradient;
          box-shadow: $G-default-shadow;
          &::after {
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            z-index: -1;
            background: $G-hover-gradient;
            opacity: 0;
            @include transition($tgs: "opacity");
          }
          .logo {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 8rem;
            height: 8rem;
            background: #464646;
            @include transition($tgs: "background, transform");

            .img {
              max-width: 80%;
              filter: invert(63%) sepia(8%) saturate(12%) hue-rotate(330deg) brightness(95%) contrast(88%);
              @include new-transition(0, $tgs: "filter, transform");
            }
          }
          .tit {
            margin: 2.7rem 0 0 0;
            font-size: 2.4rem;
            font-weight: 700;
          }
          .desc {
            color: #666666;
            margin-top: 0.4rem;
            font-size: 1.8rem;
            font-weight: 400;
            line-height: 2.6rem;
            @include transition($tgs: "color");
          }
          &:hover {
            border: 1px solid $G-hover-border-color;
            transform: translateY(-2rem) scale(1.05);
            box-shadow: $G-hover-shadow;
            &::after {
              opacity: 1;
            }
            .logo {
              background: #ffffff;
              transform: scale(1.1);
              .img {
                filter: none;
              }
            }
            .desc {
              color: #ffffff;
            }
          }
        }
      }
    }
  }

  // animation ready
  [data-section="지역설정"].is-ready {
    .l-section-header {
      &__title {
        transform: translateY(20rem) scale(3);
      }
      &__description {
        opacity: 0;
        transform: translateY(4rem);
      }
    }
    .list-regional {
      &__item {
        .btn-regional {
          opacity: 0;
          transform: translateY(4rem);
          .logo {
            transform: scale(0.4);
          }
        }
      }
    }
  }

  // animation start
  [data-section="지역설정"].is-start {
    .l-section-header {
      &__title {
        transform: translateY(0) scale(1);
        @include new-transition($tgs: "opacity, transform", $time: 0.7s, $delay: 0.2s);
      }
      &__description {
        opacity: 1;
        transform: translateY(0);
        @include new-transition($tgs: "opacity, transform", $time: 0.7s, $delay: 0.5s);
      }
    }
    .list-regional {
      @for $i from 0 through 6 {
        &__item:nth-child(#{$i}) {
          .btn-regional {
            opacity: 1;
            transform: translateY(0);
            @include new-transition($time: 0.7s, $delay: #{0.4 + $i * 0.1}s, $tgs: "opacity, transform");

            .logo {
              transform: scale(1);
              @include new-transition($time: 0.7s, $delay: #{0.45 + $i * 0.1}s, $tgs: "transform");
            }
          }
        }
      }
    }
  }

  // animation complete
  [data-section="지역설정"].is-complete {
    .list-regional {
      &__item {
        .btn-regional {
          @include new-transition($tgs: "background, transform, box-shadow", $time: "0.18s, 0.18s, 0.3s", $important: true);
        }
      }
    }
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */
}
